import default_pic_selector from '../default_pic_selector.vue'
import { VueEditor } from 'vue2-editor'
import { mapState } from 'vuex'

export default {
  data(){
    return {
      company: {
        year: "2017",
        established_time: new Date().toLocaleDateString(),
        short_description: "",
        hashtag: []

      },
      create_mode: false,
    }
  },
  mounted(){
    if (this.$route.params.id){
      this.axios.get("/api/spa/company/"+this.$route.params.id).then(res=>{
        this.company=res.data
        // if (!this.company.hashtag){
        //   this.company.hashtag=[]
        // }
        // if (typeof this.company.hashtag=="string"){
        //   this.company.hashtag=JSON.parse(this.company.hashtag)
        // }
      })
    }else{
      this.create_mode=true
    }
  },
  components:{
    VueEditor,
    default_pic_selector, 
  },
  methods: {
    select_pic(obj){
      console.log(obj)
      this.company.cover=obj.url
    },
    handleSave(){
      if (!this.create_mode){
        this.axios.patch(
          "/api/spa/company/"+this.$route.params.id,
          this.company
        ).then(res=>{
          console.log(res.data)
          if (res.data.status=="success"){
            this.$message({
              message: '儲存成功',
              type: 'success'
            });
          }else{
            
            this.$message.error({
              message: '儲存失敗',
              // type: 'error'
            });
  
          }
        })

      }else{
        this.axios.post(
          "/api/spa/company",
          this.company
        ).then(res=>{

          this.$message({
            message: '建立成功',
            type: 'success'
          });
          // let _this  = this
          console.log(res.data.data.id)
          // setTimeout(function(){
          this.$router.push("/company/"+res.data.data.id)
          // },300)
        })

      }
    }
  },
  computed: {
    ...mapState(['companies','catas']),
    year_catas(){
      return this.catas.filter(o=>o.year==this.post.year)
    }
  }
}



